import React, { useEffect, useState, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import truncateEthAddress from 'truncate-eth-address'
import { connect } from "./redux/blockchain/blockchainActions";
import { fetchData } from "./redux/data/dataActions";
import * as s from "./styles/globalStyles";
import styled from "styled-components";
import "./App.css";
import Icons from "./components/Icons";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import abi from "./abi/abi.json";
import catgif from "./assets/cats.gif";
import logo from "./assets/logo.png";
import twitterImg from "./assets/twitter.svg";
import openseaImg from "./assets/opensea.svg";
import bgImg from "./assets/bg.png";
// Ethers
import { ethers } from "ethers";

const truncate = (input, len) =>
  input.length > len ? `${input.substring(0, len)}...` : input;

export const StyledButton = styled.button`
  padding: 10px;
  border-radius: 50px;
  border: none;
  background-color: black;
  padding: 10px;
  font-weight: bold;
  color: var(--secondary-text);
  width: 100px;
  cursor: pointer;
  box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  -webkit-box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  -moz-box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  :active {
    box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
  }
`;

export const StyledRoundButton = styled.button`
  padding: 10px;
  border-radius: 100%;
  border: none;
  background-color: black;
  padding: 10px;
  font-weight: bold;
  font-size: 15px;
  color: var(--accent-text);
  width: 30px;
  height: 30px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 4px 0px -2px rgba(250, 250, 250, 0.3);
  -webkit-box-shadow: 0px 4px 0px -2px rgba(250, 250, 250, 0.3);
  -moz-box-shadow: 0px 4px 0px -2px rgba(250, 250, 250, 0.3);
  :active {
    box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
  }
`;

export const ResponsiveWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: stretched;
  align-items: stretched;
  width: 100%;
  @media (min-width: 767px) {
    flex-direction: row;
  }
`;

export const StyledLogo = styled.img`
  width: 200px;
  @media (min-width: 767px) {
    width: 300px;
  }
  transition: width 0.5s;
  transition: height 0.5s;
`;

export const StyledImg = styled.img`
  box-shadow: 0px 5px 11px 2px rgba(0, 0, 0, 0.7);
  border: 4px dashed var(--secondary);
  background-color: var(--accent);
  border-radius: 100%;
  width: 200px;
  @media (min-width: 900px) {
    width: 250px;
  }
  @media (min-width: 1000px) {
    width: 300px;
  }
  transition: width 0.5s;
`;

export const StyledLink = styled.a`
  color: var(--secondary);
  text-decoration: none;
`;

function App() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const data = useSelector((state) => state.data);
  const [claimingNft, setClaimingNft] = useState(false);
  const [feedback, setFeedback] = useState(`Click buy to mint your NFT.`);
  const [mintAmount, setMintAmount] = useState(1);

  const [CONFIG, SET_CONFIG] = useState({
    CONTRACT_ADDRESS: "",
    SCAN_LINK: "",
    NETWORK: {
      NAME: "",
      SYMBOL: "",
      ID: 0,
    },
    NFT_NAME: "",
    SYMBOL: "",
    MAX_SUPPLY: 1,
    WEI_COST: 0,
    DISPLAY_COST: 0,
    GAS_LIMIT: 0,
    MARKETPLACE: "",
    MARKETPLACE_LINK: "",
    SHOW_BACKGROUND: false,
  });

  const [totalSupply, setTotalSupply] = useState(0);

  const claimNFTs = () => {
    let cost = CONFIG.WEI_COST;
    let gasLimit = CONFIG.GAS_LIMIT;
    let totalCostWei = String(cost * mintAmount);
    let totalGasLimit = String(gasLimit * mintAmount);

    console.log("Cost: ", totalCostWei);
    console.log("Gas limit: ", totalGasLimit);
    setFeedback(`Minting your ${CONFIG.NFT_NAME}...`);
    setClaimingNft(true);
    blockchain.smartContract.methods
      .mintAllowListBool(mintAmount)
      .send({
        to: CONFIG.CONTRACT_ADDRESS,
        from: blockchain.account,
        value: totalCostWei,
        gasLimit: null,
        maxFee: null,
        maxPriority: null,
      })
      .once("error", (err) => {
        console.log(err);
        setFeedback("Sorry, something went wrong please try again later.");
        setClaimingNft(false);
      })
      .then((receipt) => {
        console.log(receipt);
        setFeedback(
          `WOW, the ${CONFIG.NFT_NAME} is yours! go visit Opensea.io to view it.`
        );
        setClaimingNft(false);
        dispatch(fetchData(blockchain.account));
      });
  };

  const decrementMintAmount = () => {
    let newMintAmount = mintAmount - 1;
    if (newMintAmount < 1) {
      newMintAmount = 1;
    }
    setMintAmount(newMintAmount);
  };

  const incrementMintAmount = () => {
    let newMintAmount = mintAmount + 1;
    if (newMintAmount > 10) {
      newMintAmount = 10;
    }
    setMintAmount(newMintAmount);
  };

  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };

  const getRemaining = async () => {
    const provider = new ethers.providers.JsonRpcProvider(
      // "https://mainnet.infura.io/v3/5fd7bcc223cb4943817b92c7aba83941" //Ethereum Mainnet
      "https://rinkeby.infura.io/v3/5fd7bcc223cb4943817b92c7aba83941" //Ethereum Test Rinkeby
    );
    // console.log(CONFIG.CONTRACT_ADDRESS);

    const contract = new ethers.Contract(
      CONFIG.CONTRACT_ADDRESS,
      abi,
      provider
    );
    // console.log(contract);

    const total = await contract.totalSupply();
    // console.log(await total.toString());
    setTotalSupply(await total.toString());
  };

  const getConfig = async () => {
    const configResponse = await fetch("/mint/config/config.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const config = await configResponse.json();
    console.log(config);
    SET_CONFIG(config);
  };

  useEffect(async () => {
    getConfig();
  }, []);

  useEffect(() => {
    getRemaining();
  }, [CONFIG]);

  useEffect(() => {
    getData();
  }, [blockchain.account]);

  var settings = {
    dots: true,
    arrow: false,
    infinite: true,
    vertical: true,
    autoplay: true,
    autoplaySpeed: 2000,
    speed: 500,
    slidesToShow: 2,
    slidesToScroll: 1,
    pauseOnHover: false,
    responsive: [
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 2,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  return (

    <div className="sc-bczRLJ hJwjhJ">
  <div
    className="sc-jSMfEi fVvNmO"
    style={{ backgroundImage: `url(${bgImg})` }}
  >
    <div className="sc-hHLeRK eFoWWp">
      <div className="sc-idiyUo fKmiSA">
        <a href="https://messycats.xyz/" target="_blank">
          <img
            alt="logo"
            src={logo}
            className="sc-dmRaPn lhqOCS"
          />
        </a>
        <p className="sc-jqUVSM fhBssc">Messy Cats</p>
      </div>
      <div className="sc-fnykZs eVJqTk">
        <a
          href="https://messycats.xyz/#about"
          target="_blank"
          className="sc-breuTD bVsUYs"
        >
          About Us
        </a>
        
        <a
          href="https://messycats.xyz/#faq"
          target="_blank"
          className="sc-breuTD bVsUYs"
        >
          Faq
        </a>
       
      </div>
      <div className="sc-hAZoDl flYiLe">
        <a href="https://twitter.com/MessyCatsNFT" target="_blank">
          <img
            src={twitterImg}
            alt="twitter"
            className="sc-ksZaOG SrcCT"
          />
        </a>
       
        <a href="https://opensea.io/collection/messy-cats-v2" target="_blank">
          <img
            src={openseaImg}
            alt="opensea"
            className="sc-ksZaOG SrcCT"
          />
        </a>
      </div>
      <div className="sc-bBrHrO gPrmVh">
        <p onClick={(e)=>{
           e.preventDefault();
           dispatch(connect());
           getData();
        }}
          className="sc-jqUVSM fhBssc"
          style={{ fontSize: "1rem", color: "rgb(11, 14, 39)" }}
          
        >
         {(blockchain?.account)?(truncateEthAddress(blockchain.account)) : ("Connect Wallet")} 
        </p>
      </div>
    </div>
    <div className="sc-eCYdqJ dUdpsG" />
    <div className="sc-jSMfEi erJiDN" style={{flex:"0.3"}}>
      <p className="sc-gKXOVf grSBcS">Mint your Messy Cats</p>
    </div>
    <div className="sc-dkzDqf CmDGU" />
    <div className="sc-dIouRR jBsQG" style={{ padding: 24 }}>
      <img
        src={catgif}
        alt="cats"
        className="sc-kgflAQ kLAnxN"
      />
      <div className="sc-dkzDqf CmDGU" />
      <div className="sc-jSMfEi erJiDN">
        <p
          className="sc-iBkjds gPzcdo"
          style={{
            textAlign: "center",
            color: "var(--accent-text)",
            fontFamily: "coder"
          }}
        >
          <s.TextTitle
                style={{
                  textAlign: "center",
                  color: "var(--accent)",
                  fontFamily: "coder"
                }}
                className=".gPzcdo"

              >
                {totalSupply} | {CONFIG.MAX_SUPPLY}
              </s.TextTitle>
        </p>
        <div class="sc-dkzDqf CmDGU"></div>
        <p className="sc-papXJ QHbmV">{CONFIG.DISPLAY_COST} {CONFIG.NETWORK.SYMBOL}</p>
        <div class="sc-dkzDqf CmDGU"></div>
        <p className="sc-jqUVSM fhBssc">1x Messy Cats Unique</p>
        <div class="sc-dkzDqf CmDGU"></div>
        <p className="sc-jqUVSM fhBssc">{CONFIG.DISPLAY_COST} {CONFIG.NETWORK.SYMBOL} Per NFT</p>
        <div class="sc-dkzDqf CmDGU"></div>
        <div className="sc-jSMfEi hVRPDw">
          <s.Container
              flex={2}
              jc={"center"}
              ai={"center"}
              style={{
                // backgroundColor: "#f5efef",
                // padding: "48px 24px",
                // borderRadius: 24,
                // width: "75%",
                // margin: "30px auto",
                // // border: "4px solid black",
                // border: "10px inset rgba(0,0,0,1)",
                // boxShadow: "0px 5px 11px 2px rgba(0,0,0,0.7)",
              }}
            >                     
              {Number(totalSupply) >= CONFIG.MAX_SUPPLY ? (
                <>
                  <s.TextTitle
                    style={{ textAlign: "center", color: "var(--accent)" }}
                  >
                    The sale has ended.
                  </s.TextTitle>
                  <s.TextDescription
                    style={{ textAlign: "center", color: "var(--accent)" }}
                  >
                    You can still find {CONFIG.NFT_NAME} on
                  </s.TextDescription>
                  <StyledLink target={"_blank"} href={CONFIG.MARKETPLACE_LINK}>
                    {CONFIG.MARKETPLACE}
                  </StyledLink>
                </>
              ) : (
                <>
                  <s.SpacerXSmall />
                  {blockchain.account === "" ||
                  blockchain.smartContract === null ? (
                    <s.Container ai={"center"} jc={"center"}>
                      {/* <s.TextDescription
                        style={{
                          textAlign: "center",
                          color: "var(--accent)",
                        }}
                      >
                        Connect to the {CONFIG.NETWORK.NAME} network
                      </s.TextDescription> */}
                      <button
                      className="sc-fEOsli FOJDs"
                        onClick={(e) => {
                          e.preventDefault();
                          dispatch(connect());
                          getData();
                        }}
                      >
                        CONNECT
                      </button>
                      {blockchain.errorMsg !== "" ? (
                        <>
                          <s.TextDescription
                            style={{
                              textAlign: "center",
                              color: "var(--accent)",
                            }}
                          >
                            {blockchain.errorMsg}
                          </s.TextDescription>
                        </>
                      ) : null}
                    </s.Container>
                  ) : (
                    <>
                      
                      <s.Container ai={"center"} jc={"center"} fd={"row"} className="sc-jSMfEi hJsxXs" style={{maxWidth:"250px", justifyContent: "space-around"}}>
                        <button
                          style={{ lineHeight: 0.4 }}
                          disabled={claimingNft ? 1 : 0}
                          onClick={(e) => {
                            e.preventDefault();
                            decrementMintAmount();
                          }}
                          className="sc-bjUoiL eBBEKR"
                        >
                          -
                        </button>
                        <s.SpacerMedium />
                        <s.TextDescription
                          style={{
                            textAlign: "center",
                            color: "var(--primary-text)",
                            fontSize: "30px",
                            lineHeight: "1.6"}}
                          className="sc-iqcoie bxndgp"
                        >
                          {mintAmount}
                        </s.TextDescription>
                        <s.SpacerMedium />
                        <button
                          disabled={claimingNft ? 1 : 0}
                          onClick={(e) => {
                            e.preventDefault();
                            incrementMintAmount();
                          }}
                          className="sc-bjUoiL eBBEKR"
                        >
                          +
                        </button>
                      </s.Container>
                      <s.SpacerSmall />
                      <p class="sc-ftvSup pVfmA">Total     {(CONFIG.DISPLAY_COST*mintAmount).toFixed(4)} {CONFIG.NETWORK.SYMBOL}</p>
                      <s.SpacerSmall />

                      <s.Container ai={"center"} jc={"center"} fd={"row"}>
                      <div class="sc-jSMfEi hVRPDw">
                        <button disabled={claimingNft ? 1 : 0}
                          onClick={(e) => {
                            e.preventDefault();
                            claimNFTs();
                            getData();
                          }}
                          class="sc-fEOsli FOJDs">{claimingNft ? "MINTING..." : "MINT NOW"}</button></div>
                          
                      </s.Container>
                      <div class="sc-dkzDqf CmDGU"></div>
                      <p class="sc-jqUVSM fhBssc">Max 10 Per Tx</p>
                      <div class="sc-dkzDqf CmDGU"></div>
                      <s.TextDescription
                        class="sc-jqUVSM fhBssc"
                      >
                        {feedback}
                      </s.TextDescription>
                    </>
                  )}
                </>
              )}
              <s.SpacerMedium />
            </s.Container>
        </div>
      </div>
    </div>
  </div>
</div>

  );
}

export default App;
